<template>
  <div style="margin-top:25px;">
    <el-row class="row-flex">
      <el-col :span="15">
        <el-main>
          <el-steps :active="3" align-center>
            <el-step title="Step 1" icon="el-icon-user" description="Create an account" />
            <el-step title="Step 2" icon="el-icon-upload" description="Upload credentials" />
            <el-step title="Step 3" icon="el-icon-circle-check" description="Complete trainning" />
            <el-step title="Step 4" icon="el-icon-edit-outline" description="Start rating" />
          </el-steps>
        </el-main>
      </el-col>
    </el-row>
    <el-row class="row-flex">
      <el-col :span="15">
        <el-main>
          <div v-if="isLoadAccountFinished">
            <div v-if="!isCompletedOnboard" style="display: flex; align-items: center;">
              <button id="account-link-button" :disabled="disable" class="connect-button" @click="onClick"><span>Connect with Stripe</span></button>
              <div style="padding-left: 20px">
                {{ text }} <i v-if="disable" class="el-icon-loading" />
              </div>
            </div>
            <div v-if="isCompletedOnboard">
              You've connected to Stripe.
            </div>
          </div>
        </el-main>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import paymentService from '../../services/payment.service'
import accountService from '../../services/account.service'

export default {
  name: 'StartRating',
  data() {
    return {
      disable: false,
      text: '',
      accountId: null,
      isLoadAccountFinished: false,
      isCompletedOnboard: false
    }
  },
  computed: {
    currentUser() {
      return this.$store.getters['auth/getUser']
    }
  },
  mounted() {
    this.onLoad()
  },
  methods: {
    onLoad() {
      accountService.isCompleteOnboard(this.currentUser.id).then(rs => {
        console.log('IS USER COMLETED ONBOARD...', rs)
        this.isLoadAccountFinished = true
        this.isCompletedOnboard = rs
      })

      // if (this.$route.params.accountId) {
      //   this.accountId = this.$route.params.accountId
      // }
      // paymentService.getStripeAccount(this.currentUser.id).then(rs => {
      //   if (rs) {
      //     this.$router.push('/rater/StartRating/' + rs.id)
      //     this.accountId = rs.id
      //   } else {
      //     this.$router.push('/rater/StartRating/')
      //   }
      // })
    },
    onClick() {
      this.disable = true
      this.text = 'Opening'
      paymentService.createAccount(this.currentUser.id).then(rs => {
        console.log(rs)
        if (rs.url) {
          window.location.href = rs.url
        } else {
          this.disable = false
          this.text = 'Something went wrong!'
        }
      })
    }
  }

}
</script>
<style scoped>

.row-flex {
  display: flex;
  justify-content: center;
  margin: 25px 0;
}

.el-main{
  border: 1px solid #c5c5c5;
  border-radius: 4px;

}
.connect-button {
    display: inline-block;
    margin-bottom: 1px;
    background-image: -webkit-linear-gradient(#28A0E5, #015E94);
    background-image: -moz-linear-gradient(#28A0E5, #015E94);
    background-image: -ms-linear-gradient(#28A0E5, #015E94);
    background-image: linear-gradient(#28A0E5, #015E94);
    -webkit-font-smoothing: antialiased;
    border: 0;
    padding: 1px;
    height: 32px;
    text-decoration: none;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    border-radius: 4px;
    -moz-box-shadow: 0 1px 0 rgba(0,0,0,0.2);
    -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-decoration: none !important;
}
.connect-button span:before, .connect-button.blue span:before {
    background-image: url(data:image/png;base64,);
}
.connect-button span:before {
    content: '';
    display: block;
    position: absolute;
    left: 11px;
    top: 50%;
    width: 23px;
    height: 24px;
    margin-top: -12px;
    background-repeat: no-repeat;
    background-size: 23px 24px;
}
.sr-main {
    justify-content: left;
    padding: 12px 0 0 0;
}
.sr-main {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 25px 0 0 0;
    background: var(--body-color);
}
.connect-button span {
    padding-left: 44px;
}
.connect-button span {
    display: block;
    position: relative;
    padding: 0 5px 0 40px;
    height: 30px;
    background: #1275FF;
    background-image: -webkit-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
    background-image: -moz-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
    background-image: -ms-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
    background-image: linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
    font-size: 15px;
    line-height: 30px;
    color: white;
    font-weight: bold;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);
    -moz-box-shadow: inset 0 1px 0 rgba(255,255,255,0.25);
    -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    border-radius: 3px;
}
.connect-button:disabled {
    opacity: 0.5;
}
</style>
